@import "variables";

* {
  font-family: 'Open Sans', sans-serif !important;
}

body {
  font-size: 14px;
}

.primary-color {
  color: $primary-color !important;
}

a, .btn {
  transition: all 300ms ease-in-out;
  text-decoration: none !important;
  color: $primary-color;
}

hr.section-divider {
  border-color: #e8e8e8;
  margin: 40px 0;
}

.input-group-text { background: $primary-color; color: #fff; border-color: $primary-color; cursor: pointer; }

.btn.btn-default.read-more, .default-btn { float: right; border: 1px solid #E8E8E8; color: #1FA67A; background-color: transparent; margin-top: 10px;
  font-size: 14px; font-weight: 600;
  &:hover { background-color: $primary-color; color: #fff; }
}

.thumbnail {
  display: block;
  padding: 4px;
  margin-bottom: 20px;
  line-height: 1.42857143;
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 4px;
  -webkit-transition: border .2s ease-in-out;
  -o-transition: border .2s ease-in-out;
  transition: border .2s ease-in-out;
}

.tab-thumb {
  margin-right: 8px;
  margin-bottom: 5px;
  padding-top: 5px;
  float: left;
  line-height: 0;
  width: 60px;
  height: 60px;
}

.main-header {
  .navbar { padding: 0; background-color: #fff; font-weight: 200; margin-bottom: 0; border-bottom: 1px solid #eee;
    .navbar-brand {
      h1 { font-size: 24px; margin: 8px 0; color: $primary-color; text-transform: uppercase; font-weight: 600; }
    }
    .navbar-nav {
      .nav-item {
        .nav-link {
          color: #999; line-height: 30px; padding: 18px 15px; transition: all 300ms ease-in-out; font-size: 14px;
          &:focus, &:hover { background-color: $primary-color; color: #fff; }
        }
      }
    }
  }
}

.main-page-carousel {
  .carousel-item img { min-height: 220px;
    @media screen and (min-width: 579px) {
      min-height: 340px;
    }
    @media screen and (min-width: 992px) {
      min-height: 450px;
    }
  }
  .carousel-caption { text-align: left;
    h5 { background: rgba(31, 166, 122, 0.7); font-weight: 400; font-size: 24px; max-width: 640px;
      display: inline-block; padding: 12px 20px; margin-bottom: 0; color: #fff;
    }
    p { background: rgba(59, 59, 59, 0.8);display: block;color: #ddd;max-width: 600px;padding: 12px 20px; }
  }
}

article.blog-post-content {
  header { padding-bottom: 9px; margin: 0 0 20px; border-bottom: 1px solid #eee;
    h3 { color: #444; font-size: 24px; margin: 20px 0 10px;
      a { color: #444;
        &:hover { color: $primary-color; }
      }
    }
  }
  .post-content { color: #6b6b6b; font-size: 13px;

  }
}
.header-meta { font-size: 13px; color: #666;
  i, svg { color: $primary-color; }
}

.sidebar-widget { margin-bottom: 25px;
  .widget-title { color: $primary-color; font-size: 20px; text-transform: uppercase; letter-spacing: 0.02em; font-weight: 500; margin-bottom: 15px; }
  .widget-content { border: 1px solid #e8e8e8; padding: 10px;
    li.media { overflow: hidden; list-style: none; border-bottom: 1px solid #f0f0f0; margin: 0 0 8px; padding: 0 0 6px;
      &:last-child { border-bottom: none; margin-bottom: 0; padding-bottom: 0; }
    }
  }
}

.single-page-content {
  header { border-bottom: 1px solid #eee; padding-bottom: 10px;
    h1 { font-size: 32px; color: #444; margin: 20px 0 10px 0; }
  }
  main { color: #6b6b6b; line-height: 25px; letter-spacing: 0.02em; padding: 20px 0;
    .share-this { margin: 15px 0 12px 0; display: inline-block; line-height: 1.2; font-size: 13px; font-weight: 700;
      text-transform: uppercase; color: $primary-color;
      &:before { content: ""; display: block; width: 100%; min-width: 30px; border-top: 1px solid #ddd; margin-bottom: 1em; }
    }
    .share-links { padding: 0!important; margin: 0 0 .7em 0!important; list-style: none!important;
      li { margin: 0 5px 5px 0; padding: 0; display: inline-block;
        a { text-decoration: none!important; display: inline-block; font-size: 12px; font-weight: 400; border-radius: 3px;
          color: #656565!important; background: #f8f8f8; border: 1px solid #ccc; box-shadow: 0 1px 0 rgba(0,0,0,.08); text-shadow: none; line-height: 23px; padding: 1px 8px 0 5px; }
      }
    }
  }
}

.main-footer {
  .footer-content { background-color: #313233; padding: 40px 0 20px 0; overflow: hidden; color: #ccc;
    a { color: #ccc; }
    .footer-widget {
      h3 { font-size: 20px; color: inherit; margin-top: 0; }
      .footer-nav-links { padding: 10px;
        li {
          a { border-bottom: 1px solid #444; padding: 10px 0; }
        }
      }
    }
  }
  .footer-bottom { background-color: #1F1F1F; border-top: 1px solid #3A3A3A;
    .copyright {  color: #ccc; padding: 22px 0; }
  }
}
